.checkoutFormBackground {
    background-color: purple;
    height: 100vh;
    background-image: url(./checkoutFormBackground.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}

.header {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 30px;
    font-weight: 700;
    padding-bottom: 40px;
    padding-top: 40px;
    text-align: center;
  }
  
  .paymentFormContainer {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  .paymentElementcontainer {
    padding-bottom: 20px;
  }
  
  .button {
    background-color: thistle;
    border-radius: 16px;
    cursor: pointer;
    padding: 4px 16px;
  }
  
  .emailLabel {
    margin-bottom: 3px;
  }
  
  .emailInput {
    border: 1.5px solid grey;
    border-radius: 5px;
    margin-bottom: 15px;
    margin-right: 10px;
    padding: 10px;
    width: 93%;
  }
